export default {
  data() {
    return {
      enter: true,
      Show: false,
      rwdHeight: {
        height: "90px",
      },
      fullWidth: 0,
      fullHeight: 0,
    };
  },
  mounted() {
    const vm = this;
    vm.fullWidth = window.innerWidth;
    vm.fullHeight = window.innerHeight;
    window.onresize = () => {
      vm.fullWidth = window.innerWidth;
      vm.fullHeight = window.innerHeight;
      if (vm.fullWidth > 765) {
        this.rwdHeight.height = "90px";
      }
    };
  },
  methods: {
    change() {
      this.Show = !this.Show;
      if (this.Show === true) {
        this.rwdHeight.height = "160px";
      } else {
        this.rwdHeight.height = "90px";
      }
    },
    alreadyBtn() {
      this.Show = !this.Show;
      this.rwdHeight.height = "90px";
    },
  },
};


