import { Loader } from "@googlemaps/js-api-loader";
import { mapState } from "vuex";

const loader = new Loader({
  apiKey: "AIzaSyC4DPl7G4rZgvzA3onOrIkrpYHD5Xo4dTc",
  version: "weekly",
});
export default {
  name: "map",
  data() {
    return {
      mapdata: [],
      map: null,
      // 預設經緯度
      lat: 25.12731,
      lng: 121.51872,
      infowindow: null,
    };
  },
  computed: {
    ...mapState(["nowMountain"]),
  },
  mounted() {
    this.lat = this.nowMountain.googleMapLocation.lat;
    this.lng = this.nowMountain.googleMapLocation.lng;
    this.mapdata = [
      {
        name: this.nowMountain.name,
        lat: this.nowMountain.googleMapLocation.lat,
        lng: this.nowMountain.googleMapLocation.lng,
      },
    ];
    this.initMap();
    this.setMarker();
  },
  methods: {
    initMap() {
      //透過 Map 物件建構子建立新地圖 map 物件實例，並將地圖呈現在 id 為 map 的元素中
      loader.load().then((google) => {
        this.map = new google.maps.Map(document.getElementById("map"), {
          // 設定地圖的中心點經緯度位置
          center: { lat: this.lat, lng: this.lng },
          // 設定地圖縮放比例 0-20
          zoom: 13,
        });
      });
    },

    // 建立地標
    setMarker() {
      // 為每間餐廳都建立地標、訊息視窗、事件監聽
      loader.load().then((google) => {
        this.mapdata.forEach((location) => {
          const marker = new google.maps.Marker({
            // 設定為該餐廳的座標
            position: { lat: location.lat, lng: location.lng },
            map: this.map,
          });
          // 建立訊息視窗
          const infowindow = new google.maps.InfoWindow({
            content: `
            <div id="content">
              <p id="firstHeading" class="firstHeading">${location.name}</p>
            </div>
          `,
            maxWidth: 200,
          });
          // 綁定點擊事件監聽
          marker.addListener("click", () => {
            // 如果目前有開啟中的訊息視窗，先將其關閉
            if (this.infowindow) {
              this.infowindow.close();
            }
            // // 顯示被點擊地標的訊息視窗
            infowindow.open(this.map, marker);
            // // 存入目前開啟的訊息視窗
            this.infowindow = infowindow;
          });
        });
      });
    },
  },
};
